.action-buttons {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}
.action-buttons p-button {
  margin: 0;
}

p-table ::ng-deep .p-datatable .p-datatable-thead > tr > th {
  padding: 1rem;
  background-color: var(--primary-color);
  color: var(--primary-contrast-color);
  font-weight: 600;
}
p-table ::ng-deep .p-datatable .p-datatable-tbody > tr:hover {
  background-color: var(--surface-hover);
}
p-table ::ng-deep .p-datatable .p-datatable-tbody > tr > td {
  padding: 1rem;
  vertical-align: middle;
}
p-table ::ng-deep .p-datatable .p-datatable-emptymessage > tr > td {
  padding: 2rem;
  text-align: center;
  color: var(--text-color-secondary);
}
p-table ::ng-deep .p-paginator {
  padding: 1rem;
  background-color: var(--surface-overlay);
  border: 1px solid var(--surface-border);
}

p-toolbar ::ng-deep .p-toolbar {
  padding: 1rem;
  background-color: var(--surface-overlay);
  border: 1px solid var(--surface-border);
  border-radius: var(--content-border-radius);
  margin-bottom: 1.5rem;
}
p-toolbar ::ng-deep .p-toolbar .p-toolbar-group-start,
p-toolbar ::ng-deep .p-toolbar .p-toolbar-group-end {
  display: flex;
  gap: 0.5rem;
  align-items: center;
}

p-tooltip ::ng-deep .p-tooltip {
  background-color: var(--surface-overlay);
  color: var(--text-color);
  border: 1px solid var(--surface-border);
}

p-iconfield ::ng-deep input {
  width: 250px;
  padding: 0.75rem 1rem 0.75rem 2.5rem;
  border: 1px solid var(--surface-border);
  border-radius: var(--content-border-radius);
  background-color: var(--surface-card);
  color: var(--text-color);
  transition: border-color var(--element-transition-duration);
}
p-iconfield ::ng-deep input:focus {
  border-color: var(--primary-color);
  outline: none;
}
p-iconfield ::ng-deep input::placeholder {
  color: var(--text-color-secondary);
}
